import React from 'react';
import Chip from 'material-ui/Chip';
const style = {
  display: 'inline-block',
  margin: '5px 10px'
}
const ClassList = ({ classes }) => (
  <div style={{ padding: '5px 10px', display: 'flex', flexWrap: 'wrap'}}>
    { 
      classes.map(c => <Chip style={style} key={c.class}>{c.class} - {c.score}</Chip>)
    }
  </div>
);

export default ClassList;


