import React, { Component } from 'react';
import axios from 'axios';

import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import CircularProgress from 'material-ui/CircularProgress';
import Paper from 'material-ui/Paper';
import Checkbox from 'material-ui/Checkbox';

import ClassList from '../../components/ClassList';
import './style.css';

const style = {
  display: 'flex',
  margin: '20px 15px',
  height: '180px'
}

class FoodSearch extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
      classifiers: ['default'],
      result: null,
      isProcessing: false
    }

    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleCheck = this.handleCheck.bind(this);
    // this.getHistory = this.getHistory.bind(this);
    this.setCheck = this.setCheck.bind(this);
  }

  // componentDidMount() {
  //   this.getHistory();
  // }

  // getHistory() {
  //   axios.post('api/food/history', {
  //     classifiers: this.state.classifiers
  //   })
  //   .then(res => {
  //     this.setState({
  //       result: res.data
  //     });
  //   })
  //   .catch(err => console.error(err));
  // }

  handleSubmit() {
    this.setState({
      isProcessing: true
    });

    axios.post('/watson/analyze', {
      classifiers: this.state.classifiers,
      url: this.state.value
    })
    .then(res => {
      console.log(res);
      this.setState({
        result: res.data,
        isProcessing: false
      });
    })
    .catch(err => console.error(err));
  }

  handleChange({ target }) {
    this.setState({
      value: target.value
    });
  }

  setCheck(value) {
    return this.state.classifiers.indexOf(value) > -1;
  }

  handleCheck(value) {
    const id = this.state.classifiers.indexOf(value);

    if (id > -1) {
      this.setState({
        classifiers: this.state.classifiers.filter(c => c !== value)
      });
    } else {
      let tmp = this.state.classifiers.filter(c => c !== value);
      tmp = tmp.concat([ value ]);
      this.setState({
        classifiers: tmp
      });
    }
  }

  render() {
    let result = [];

    if (this.state.result && Array.isArray(this.state.result) && this.state.result.length > 0) {
      this.state.result.forEach(r => {
        const classLists = r.classifiers.map(classifier => <ClassList classes={classifier.classes} /> );

        result.push(
          <Paper zDepth={1} style={style}>
            <img src={r.url} height='100%' width='auto' />
            <div style={{ width: '100%' }}>
              { classLists }
            </div>
          </Paper>
        );
      });
    }

    const { isProcessing } = this.state;
    // <RaisedButton label="歴史" secondary={true} onClick={this.getHistory} disabled={isProcessing} />
    return (
      <div>
        <Paper zDepth={1} style={{ padding: '10px 15px', margin: '5px'}} >
          <Checkbox label="Default" checked={this.setCheck('default')} onCheck={() => this.handleCheck('default')} />
          <Checkbox label="Food" checked={this.setCheck('food')} onCheck={() => this.handleCheck('food')} />
          <Checkbox label="Face" checked={this.setCheck('face')} onCheck={() => this.handleCheck('face')} />
          <TextField floatingLabelText="画像URL" fullWidth={true} value={this.state.value} onChange={this.handleChange} />
          <RaisedButton label="送信" primary={true} onClick={this.handleSubmit} disabled={isProcessing} style={{ marginRight: '10px' }}/>
        </Paper>
        
        { isProcessing ? <CircularProgress /> : result } 
      </div>
    )
  }
}

export default FoodSearch;


